<template>
	
	<div class="chedui-1-2">
		<div class="chedui-top clearfix">
		    <p><span>车辆分享 / </span><span>说明说明说明</span></p>
		    <a href="javascript:history.back(-1)">返回上一页 <span class="fa fa-angle-right"></span> </a>
		</div>
	    <div class="chedui-bottom">
	        <el-table ref="multipleTable" :data="tableData3" border tooltip-effect="dark"  @selection-change="handleSelectionChange">
			<el-table-column type="selection" width="75px">
			</el-table-column>
			<el-table-column label="车辆信息" width="400px">
				<template scope="scope">{{ scope.row.date }}</template>
			</el-table-column>
			<el-table-column prop="name" label="司机信息" width="400px">
			</el-table-column>
			<el-table-column prop="address" label="定位信息" show-overflow-tooltip  width="400px">
			</el-table-column>
		</el-table>
		<el-input v-model="input" placeholder="请输入内容" style='width: 320px; height: 50px;margin-left: 30px;'></el-input>
		<el-button type="success">确认分享</el-button>
	    </div>
	    
	</div>
</template>

<script>
	export default {
		data(){
			return{
				input:'',
				tableData3: [{
					date: '泸DG5060  厢式车  9.6米   十吨  30方',
					name: '王小虎   ',
					address: '山东省烟台市莱阳市G204(龙门西路)'
				}, {
					date: '泸DG5060  厢式车  9.6米   十吨  30方',
					name: '王小虎   ',
					address: '山东省烟台市莱阳市G204(龙门西路)'
				}, {
					date: '泸DG5060  厢式车  9.6米   十吨  30方',
					name: '王小虎   ',
					address: '山东省烟台市莱阳市G204(龙门西路)'
				}],
				multipleSelection: []
			}
		},
		methods: {
			toggleSelection(rows) {
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			deleteRow(index, rows) {
				rows.splice(index, 1);
			},
		}
	}
</script>

<style lang="less">
	.clearfix:after{
		display: block;
		clear: both;
		content: '';
	}
	.chedui-1-2{
		
		.chedui-top{
			width: 1340px;
			p:nth-child(1){
				height:90px;
				line-height: 90px;
				color: #8F9DC0;
				float: left;
				span:nth-child(1){
					font-size: 20px;
					font-weight: bold;
					color: #3651A2;
				}
				span:nth-child(2){
					font-size: 14px;
				}
			}
			a{
				float: right;
				line-height: 90px;
				font-size: 14px;
				cursor: pointer;
				&:hover{
					color: #8F9DC0;
				}
				span{
					font-size: 16px;
				}
			}
		}
		
		.chedui-bottom{
			width: 1340px;
			background: #FFFFFF;
			border: 1px solid #CDCDCD;
			padding-bottom: 500px;
			.el-table{
				margin: 40px 30px 30px 30px;
				width: 1275px;
			}
			tbody {
			cursor: pointer;
			
			tr{
				height: 100px;
			}
		}
		}
	}
</style>


